@import url(https://fonts.googleapis.com/css?family=Roboto:300,400,500,700&display=swap);
@import url(https://fonts.googleapis.com/icon?family=Material+Icons);
body{font-family:"Roboto Sans", serif}

.Carousel{position:relative;margin:auto}.Carousel__list{position:absolute;padding-left:0;display:-webkit-flex;display:flex}li{list-style-type:none}.focus{position:relative;height:130px;overflow:hidden;margin:50px auto 20px}form{background:#f5f5f5;color:#808080;border-top:rgba(138,212,255,0.92) solid 30px;border-radius:10px;box-sizing:border-box;padding:24px;display:-webkit-inline-flex;display:inline-flex;-webkit-flex-direction:inherit;flex-direction:inherit;width:30%;left:35%;position:relative;margin-left:auto;margin-right:auto}form:before{position:absolute;content:"Slider";text-align:center;color:rgba(117,2,2,0.7);font-size:16px;font-weight:700;top:-25px;left:45%}form label{display:block;padding-right:20px;-webkit-justify-content:space-between;justify-content:space-between;-webkit-align-items:center;align-items:center;width:25%;margin-bottom:8px}form label input{width:100%;box-sizing:flex;border:1px solid #808080;border-radius:4px;color:#808080;padding:4px;outline-color:#808080}form .checkbox{display:-webkit-flex;display:flex;-webkit-justify-content:space-around;justify-content:space-around}form .checkbox input{width:50px}.buttons{top:40%;position:absolute;display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between;border-radius:15px;height:30px}

